.main__header {
  width: 100vw;
  height: calc(100vh - 3rem);
  display: grid;
  place-items: center;
  margin-top: 3rem;
}
.main__header-container {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: space-between;
  gap: 6rem;
}

.main__header-left {
  margin-top: -3rem;
}

.main__header-left h4 {
  margin-bottom: 1rem;
  color: var(--color-secondary);
  font-weight: 400;
}

.main__header-left p {
  margin: 1rem 0 3.5rem;
  font-size: 1.1rem;
}
.main__header-right {
  display: grid;
  place-items: center;
  position: relative;
}

.main__header-circle {
  width: 25rem;
  height: 25rem;
  background: linear-gradient(75deg, var(--color-secondary), transparent);
  border-radius: 50%;
  filter: blur(15px);
  position: absolute;
}

.main__header-image {
  position: relative;
}




/* Programs styles*/


.programs{
  margin-top: 4rem;

}

.programs__wrapper{
  display:grid;
  grid-template-columns: repeat(4,1fr);
  gap:3rem;
  margin-top: 4rem;


}

.programs__program a{
  margin-top: 2.5rem;
  display:flex;
  align-items: center;
  justify-content: center;


}

.programs__program:hover a{
  background: var(--color-secondary);
  color: var(--color-gray-600);
}


/*Values **************************/
.values__container{
  display:grid;
  grid-template-columns: 38% 50%;
  gap:12%;
}

.values__image{
  filter:saturate(0.25);
  transition: var(--transition);

}
.values__image:hover{
  filter:saturate(1);
}


.values__right  p{
  margin:1.5rem 0 5rem;

}
.values__wrapper{
  display:grid;
  grid-template-columns: 1fr 1fr;
  gap:3.4rem 2.4rem;

}

.card.values___value{
  padding-top:3.5rem;
  text-align: left;
  position: relative;


}

.card.values___value span{
  position: absolute;
  top:-1.5rem;

}



/****FAQ s**************************/


.faqs__wrapper{
  margin-top:4rem;
  display:grid;
  grid-template-columns: 1fr 1fr;
  gap:1.5rem 5rem;
  align-items:flex-start;

}
.faq{
  background: var(--color-gray-500);
  padding: 2rem;
  border-radius: 1rem;
  cursor:pointer;
}
.faq div{
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.faq__icon{
  background: transparent;
  color: var(--color-gray-100);
  font-size: 1.5rem;
}

.faq p{
  margin-top: 1.5rem;
}



/*Testimonials*************/

.testimonials{
  width: 50rem;
  margin-inline:auto;
}
.testimonials__head{
  justify-content: center;
}
.card.testimonial{
  position: relative;
  text-align: left;
  margin-top: 5rem;

}
.testimonial__avatar{
  width:4rem;
  height:4rem;
  border:2px solid transparent;
  border-radius: 1.5rem;
  overflow: hidden;
  position: absolute;
  top:-2rem;
  left:calc (50% - 2rem);
  box-shadow: 0 1.5rem 2rem rgba(26,26,54,0.4);
  transition: var(--transition);
}
.testimonial:hover 
.testimonial__avatar{
  border-radius: 50%;
  border-color: var(--color-gray-400);
}

.testimonial__quote{
  font-style:italic;
  margin:1rem 0 2rem;

}

.card small.testimonial__title{
  margin-top: 0.3rem;
}

.testimonials__btn-container{
  width:fit-content;
  margin:2.5rem auto 0;
  display:flex;
  justify-content: center;
  align-items: center;
  gap:1rem;
}

.testimonials_btn{
  background: transparent;
  cursor: pointer;

}
.testimonials_btn svg{
  fill:var(--color-primary);
  font-size: 1.8rem;
  width:3rem;
  height:3rem;

}


/*********Footer **********/


footer{
  background: var(--color-primary);
  margin-top:7rem;
  padding: 7rem;
  font-size: 0.9rem;
  color:var(--color-gray-100);
}
.footer__container{
  display:grid;
  grid-template-columns:26rem 1fr 1fr 1fr;
  
  gap:6rem;

}
.footer__container article{
  display:flex;
  flex-direction: column;
  align-items: flex-start;
  gap:0.7rem
}

.footer__container article p{
  margin-top: 0.5rem;
  
}
.footer__container article h4{
  margin-bottom: 0.6rem;
  font-size: 1rem;

}
.footer__socials{
  margin-top:1.5rem;
  display:flex;
  gap:1rem;
  align-items: center;
}
.footer__socials a{
  background: var(--color-gray-100);
  padding: 0.7rem;
  border:1px solid transparent;;
  border-radius: 0.6rem;
  transition: var(--transition);
}
.footer__socials a svg{
  color: var(--color-gray-600);
}
.footer__socials a:hover{
  border-color:var(--color-gray-100);
  background: transparent;
}

.footer__copyright{
  color:var(--color-gray-100);
  text-align:center;
  padding:1.5rem 0;
  border-top: 2px solid var(--color-primary-variant);
  margin-top: 5rem;


}

